.backgroundDiv {
    box-sizing: border-box;
    background-color: #000000;
    height: 108px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 32px;
 
  }
.divLinks{
    width: fit-content;
    padding: 0px 16px;
    display: grid;
    display: grid;
    grid: 20px / auto auto auto auto auto ;
    grid-column-gap: 32px;
}
.textDefaultNav{
    width: fit-content;
    font-family: 'SEATBCN-Regular';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
text-decoration: none;
height: 20px;
/* identical to box height, or 125% */

text-align: center;

/* Neutral/White 100 */

color: #FFFFFF; 
  }
.textSelectedNav {
    width: fit-content;
    font-family: 'SEATBCN-Regular';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 20px;
text-decoration: none;
height: 20px;
/* identical to box height, or 125% */

text-align: center;

/* Neutral/White 100 */

color: #FE3B1F; 
}
.linkCart {
    text-decoration: none;
}
.carritoContainer {
    
    display: flex;
    height: 100%;
